// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import BootstrapVue from 'bootstrap-vue'
import TwitterIcon from 'vue-material-design-icons/twitter.vue'
import EmailIcon from 'vue-material-design-icons/email.vue'
import LinkedinIcon from 'vue-material-design-icons/linkedin.vue'
import GithubIcon from 'vue-material-design-icons/github-circle.vue'

import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'
import 'vue-material-design-icons/styles.css'

Vue.use(BootstrapVue)
Vue.component('twitter-icon', TwitterIcon)
Vue.component('email-icon', EmailIcon)
Vue.component('linkedin-icon', LinkedinIcon)
Vue.component('github-circle-icon', GithubIcon)

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  components: {App},
  template: '<App/>'
})



